<template>

  <div id="app" style="width:100%" > 
      <mt-header title="酷狗音乐">
        <mt-button icon="search" slot="right"></mt-button>
      </mt-header>
      <div class="navBar" >      
          <router-link to="/" exact>新歌</router-link>      
          <router-link to="/rank">排行</router-link>      
          <router-link to="/list">歌单</router-link>     
          <router-link to="/singer">歌手</router-link>      
      </div>
      <transition>
        <router-view class="routerViews" ></router-view>
      </transition>
      <!-- <router-view name="slider"/> -->

    <div class="ft-player" v-show="$store.state.showPlayer">
            <a href="javascript:;" class="ft-go-info" id="goPlay">
                <div class="ft-left">
                    <img :src="$store.state.singerImg"  class="js-ftImg" width="100%">
                </div>
                <div class="ft-center">
                    <p class="ft-desc js-ftSongName" id="ftSongName">{{$store.state.playData.songName}}</p>
                    <p class="ft-sub-desc js-ftUserName" id="ftUserName">{{$store.state.playData.singerName}}</p>
                </div>
            </a>
            <div class="ft-right">
                <i class=" js-btnPlayPause btnPlay" @click="bofang" :class="{btnPause:$store.state.playStatus}"></i>	
                <i class="btn-next js-btnNext"></i>
                <i class="ft-icon-download js-dialog-show"></i>
            </div>
            <audio :src="$store.state.muiscUrl" controls="controls" autoplay id="audio" style="display:none"></audio>
    </div>

  </div>
</template>

<script>
export default {
  name: "App",
   data(){
        return{
            muiscUrl:'',
            showPlayer:false,
            playData:[],
            singerImg:'',
            playStatus:true
        }
    },
    methods:{
        bofang(){

            let audio=document.getElementById('audio');
             if(audio.paused){
                audio.play()
                this.$store.commit('bofang',true) 
            }else{
                audio.pause()
                this.$store.commit('bofang',false) 
            }
          
        }
    },
};
</script>




