import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import NotFind from '@/components/404'
import Singer from '@/components/Singer'
import Rank from '@/components/Rank'
import List from '@/components/List'
import Rank_info from '@/components/Rank_info'
import Plist_info from '@/components/Plist_info'
import Singer_list from '@/components/Singer_list'
import Singer_list_info from '@/components/Singer_list_info'


Vue.use(Router)

export default new Router({
  mode:'history',
  routes: [
    {
      path: '/',
      component: Home
    },
    {
      path: '/rank',
      component: Rank,
      
    },
    {
      path:'/list',
      component:List
    },
    {
      path:'/singer',
      component:Singer
      // components:{
      //   default:About,
      //   slider:Slider
      // }
    },
    {
      path:'/rank/info/:id?',
      component:Rank_info

    },
    {
      path:'/plist/list/:id?',
      component:Plist_info

    },
    {
      path:'/singer/list/:id?',
      component:Singer_list

    },
    {
      path:'/singer/info/:id?',
      component:Singer_list_info

    },
    {
      path: '*',
      component: NotFind
    }
  ]
})



